.plant-box {
  width: 100%;
  height: auto;
  position: relative;
  margin-bottom: 30px; }

.plant-box img {
  width: 100%;
  height: 300px;
  -o-object-fit: cover;
     object-fit: cover; }

.plant-name {
  bottom: 60px;
  left: 0px;
  position: absolute;
  background: #dfe9e5;
  width: 200px;
  -webkit-box-shadow: 2px 2px gray;
          box-shadow: 2px 2px gray;
  border-radius: 0px 5px 5px 0px; }

.plant-name h3 {
  margin: 10px 40px; }

@media (min-width: 800px) {
  .plant-box {
    width: 45%; }
  .plant-box img {
    height: 500px; } }



